import { Component } from '@angular/core';
import { NavController, AlertController } from 'ionic-angular';
import { Camera } from '@ionic-native/camera';
import { PhotoProvider } from '../../providers/photo/photo';
//import { BarcodeProvider } from '../../providers/barcode/barcode';
import { AngularFireDatabase, FirebaseListObservable } from 'angularfire2/database-deprecated';
import { AngularFireAuth } from 'angularfire2/auth';
import { ToastController } from 'ionic-angular';
import { AutenticacaoPage } from '../autenticacao/autenticacao';


@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

    public base64Image: string;
    lista: FirebaseListObservable<any>;
    

    constructor(
        private camera: Camera,
        private photoPrvd: PhotoProvider,
        private db: AngularFireDatabase,
        private toastCtrl: ToastController,
        private angFireAuth: AngularFireAuth,
        //private barcodeprvd: BarcodeProvider,
        private alertCtrl: AlertController,
        private navCtrl: NavController      
    ) {        
        
        try {
            this.lista = db.list('/caminho_das_imagens/');
        } catch (error) {
            console.log(error);
        }
        
    }


	pickImageFromGallery(){

        try {
            //S pode pegar imagem com login
            if(this.angFireAuth.auth.currentUser != null){            
                this.camera.getPicture({
                    destinationType: this.camera.DestinationType.DATA_URL,
                    sourceType: this.camera.PictureSourceType.SAVEDPHOTOALBUM,
                    allowEdit: true,
                    quality: 30
                }).then((imageData) => {
                    this.base64Image = "data:image/jpeg;base64," + imageData;
                    this.photoPrvd.uploadPhoto(this.base64Image);
                }, (err) => {
                    console.log(err);
                });
            }
            else{
                //Alerta caso no teha login
                this.alerta();
            }
        } catch (error) {
            console.log(error);
        }

    }
    
    
    takePicture(){

        try {
            //S pode pegar imagem com login
            if(this.angFireAuth.auth.currentUser != null){

                this.camera.getPicture({
                    destinationType : this.camera.DestinationType.DATA_URL,
                    sourceType : this.camera.PictureSourceType.CAMERA,
                    encodingType: this.camera.EncodingType.PNG,
                    allowEdit: true,
                    quality: 30
                }).then((imageData) => {
                    this.base64Image = "data:image/jpeg;base64," + imageData;
                    this.photoPrvd.uploadPhoto(this.base64Image);
                }, (err) =>  {
                    console.log(err);
                });

         
            } else {
                //Alerta caso no tenha login
                this.alerta();
            }

        } catch (error) {
            console.log(error);
        }
        
       
    }
    
    /*
    baixarArquivo(nome: string){
        let storageRef = firebase.storage().ref('/Users/');
        let caminho = storageRef.child('images/'+nome);
        caminho.getDownloadURL().then(url => {
           console.log(url); // AQUI VOC J TEM O ARQUIVO
        });
    }
    */

    codigoBarras(){
        //Cdigo de barras -> nica coisa que no me deu trabalho nessa porra :)
        //this.barcodeprvd.alertaCodBarras();
    }

    /*
    salvarPromocao(promo){
        try {
            if(this.angFireAuth.auth.currentUser != null){
                this.promocoeslista = this.db.list('/promocoes_salvas/'
                + this.angFireAuth.auth.currentUser.uid);

                this.promocoeslista.push(promo);

                let toast = this.toastCtrl.create({
                    message: 'Promocao salva',
                    duration: 3000
                });
                toast.present();

            } else {
                this.alerta();
            }

        } catch (error) {
            console.log(error);
        }
    }
    */
    

    alerta(){
        this.alertCtrl.create({
            title: 'Voce no est logado.',
            message: 'Voc precisa estar logado para salvar suas promoes!',
            buttons: [
                {
                    text: 'Fazer login',
                    handler: () => {
                        this.navCtrl.push(AutenticacaoPage);
                    }
                },
                {
                    text: 'Cancel'
                }
            ]
        }).present();

    }

    /*
    irParaMapa(img_selec){
        try {
            this.navCtrl.push(MapapromocoesPage, img_selec);
        } catch (error) {
            console.log(error);
        }
    }
    */

}



